import {Note as NoteType, getNextBase, toStringFromNote, fromStringToNote} from './utils';
import React from 'react';
import {useState} from 'react';

function Note(props: {note: NoteType, setNote: (((NoteType) => void) | null)}): JSX.Element {
    if (props.setNote == null) {
    return (
        <h2 className="d-flex bd-highlight px-2"> 
            {toStringFromNote(props.note)}
        </h2>
        );
    } else {
        const [isValid, setIsValid] = useState<boolean>(true);
        const [formValue, setValue] = useState<string>(toStringFromNote(props.note));
        return (
            <input 
                type="text" 
                className={isValid? "form-control is-valid" :"form-control is-invalid"}
                value={formValue} 
                aria-label="Note" 
                aria-describedby="basic-addon1" 
                onChange={
                    (event) => {
                        try {
                            props.setNote(fromStringToNote(event.target.value));
                            setIsValid(true);
                        } catch (errror) {
                            setIsValid(false);
                        }
                        setValue(event.target.value);
                    }
                }/>);
    }
}

export default Note