import React from "react";

export type BaseNote = "A" | "B" | "C" | "D" | "E" | "F" | "G";

export type ScaleType = "NATURAL" | "HARMONIC" | "MAJOR" | "CHROMATIC";

export type Note = {
 base: BaseNote,
 isSharp: Boolean,
 isFlat: Boolean,
};

const majorScale = [2,2,1,2,2,2,1];
const harmonicScale = [2,1,2,2,1,3,1];
const naturalScale = [2,1,2,2,1,2,2];

export function getScale (root: Note, type: ScaleType): Array<Note> {
    const scale = [root];
    let order = [0];
    switch (type) {
        case 'CHROMATIC':
            let next = getNextSemitone(root);
            while (!(next.base == root.base && next.isFlat == root.isFlat && next.isSharp == root.isSharp)) {
                scale.push(next);
                next = getNextSemitone(next);
            }
            return scale;
        case 'NATURAL':
            order = naturalScale;
            break;
        case 'HARMONIC':
            order= harmonicScale;
            break;
        case 'MAJOR':
            order = majorScale;
            break;
    }
    let next = root;
    for (let times of order) {
        for(let i=0; i< times; i++) {
            next = getNextSemitone(next);
        }
        scale.push(next);
    };
    return scale;
}

function getSemitonesBetween(a: Note, b: Note): number {
    if (a.base == b.base) {
        if (a.isFlat && b.isFlat ) {
            return 0;
        }
        if (a.isFlat && !b.isSharp ) {
            return 1;
        }
        if (a.isFlat && b.isSharp) {
            return 2;
        }
        if (a.isSharp && b.isSharp) {
            return 0;
        }
    }
    if (b.base == getNextBase(a.base)) {
        if (a.base == 'B' || a.base == 'E') {
            if (a.isSharp && b.isFlat) {
                return -1;
            }
            if (a.isSharp) {
                return getSemitonesBetween({base: getNextBase(a.base), isSharp: false, isFlat:false},b);
            }
            if (a.isFlat && !b.isFlat) {
                return 1 + getSemitonesBetween(getNextSemitone(a), b);
            }
            if (a.isFlat && b.isFlat) {
                return 1;
            }
            if (b.isFlat) {
                return 0;
            }
        }
    }
    return 1 + getSemitonesBetween(getNextSemitone(a), b);
}

function getNextSemitone(a:Note): Note {
    if (a.isFlat) {
        return {base:a.base, isSharp: false, isFlat:false};
    }
    if (a.isSharp) {
        // Some notes do not have independent sharps.
        if (a.base =='E' || a.base == 'B')  {
            return  {base: getNextBase(a.base), isSharp: true, isFlat: false};
        }
        return {base: getNextBase(a.base), isSharp: false, isFlat: false};
    }
    if (a.base =='E' || a.base == 'B')  {
        return {base: getNextBase(a.base), isSharp: false, isFlat: false};
    }
    return {base: a.base, isSharp: true, isFlat: false};
}

export function getNextBase(a:BaseNote): BaseNote {
    if (a == 'G') {
        return 'A';
    }
    return String.fromCharCode(a.charCodeAt(0) + 1) as BaseNote;
}

export function toStringFromNote(a: Note): string {
    return a.base + sharpOrFlat(a);
}

export function sharpOrFlat(note: Note): string {
    if (note.isSharp ==note.isFlat) { return "";}
    if (note.isSharp) {
        return "#";
    }
    if (note.isFlat) {
    return ("b");
    }
    return "";
}

export function fromStringToNote(a:string): Note {
    return {
        base: String.fromCharCode(a.charCodeAt(0)) as BaseNote,
        isSharp: a.charAt(1) === '#',
        isFlat: a.charAt(1) === 'b',
    }
}